.categories {
  margin-top: 15px;

  &--closed {
    .categories__list {
      display: none;

      @media (min-width: $desktop-width) {
        display: flex;
      }
    }
  }

  &--opened {
    .categories__list {
      display: flex;
    }
  }

  &__toggle {
    width: 100%;
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 18px;
    background-color: $color-black;
    border: none;
    cursor: pointer;
    outline: none;

    @media (min-width: $tablet-width) {
      font-size: 20px;
    }

    @media (min-width: $desktop-width) {
      display: none;
    }

    svg {
      fill: #fff;
      margin-right: 10px;
    }
  }

  &__list {
    position: absolute;
    width: 100%;
    color: #fff;
    background-color: $color-black;
    z-index: 999;

    @media (min-width: $tablet-width) {
      width: 50%;
    }

    @media (min-width: $desktop-width) {
      position: relative;
      padding-left: 30px;
      width: 100%;
      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__item {
    font-size: 18px;
    border-bottom: 1px solid $color-grey;
    border-top: 1px solid #efefef;
    cursor: pointer;

    @media (min-width: $desktop-width) {
      text-align: center;
      border: none;
    }

    &--man {
      position: relative;

      ul {
        display: none;
        padding-left: 40px;
        padding-bottom: 5px;

        @media (min-width: $desktop-width) {
          position: absolute;
          left: -45px;
          padding-left: 0;
          padding-bottom: 0;
          min-width: 200px;
          background-color: $color-black;
          z-index: 999;
        }
      }

      li {
        a {
          padding: 0;

          @media (min-width: $desktop-width) {
            padding-top: 5px;
            padding-bottom: 5px;
          }
        }
      }

      &:hover,
      &:focus {
        ul {
          display: flex;
        }
      }
    }

    &--sale {
      background-color: $color-pink;
      text-transform: uppercase;

      @media (min-width: $desktop-width) {
        width: 10%;

      }
    }

    &:first-child {
      border-top: 1px solid $color-grey;

      @media (min-width: $desktop-width) {
        border: none;
      }
    }

    a {
      display: inline-block;
      padding-left: 10px;
      padding-top: 5px;
      padding-bottom: 5px;
      width: 100%;
      color: #fff;
      font-size: 14px;
      border-bottom: 2px solid transparent;

      @media (min-width: $tablet-width) {
        font-size: 16px;
      }

      @media (min-width: $desktop-width) {
        padding-left: 0;
        padding-top: 18px;
        padding-bottom: 18px;
      }

      &:hover,
      &:focus {
        color: $color-brown;

        @media (min-width: $desktop-width) {
          border-color: $color-brown;
        }
      }
    }
  }
}
