.features {
  padding-top: 20px;
  padding-bottom: 40px;

  @media (min-width: $desktop-width) {
    padding-top: 60px;
    padding-bottom: 80px;
  }

  &__list {

    @media (min-width: $desktop-width) {
      padding-left: 50px;
      padding-right: 50px;
      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__item {
    position: relative;
    font-size: 10px;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 1px;

    @media (min-width: $tablet-width) {
      font-size: 14px;
    }

    @media (min-width: $desktop-width) {
      max-width: 235px;
      text-align: left;

      &:last-child {
        &::after {
          display: none;
        }
      }
    }

    &::after {
      position: absolute;
      content: "";
      bottom: 0;
      left: 38%;
      width: 80px;
      height: 1px;
      background-color: $color-grey;

      @media (min-width: $tablet-width) {
        width: 180px;
      }

      @media (min-width: $desktop-width) {
        top: 12px;
        right: -93px;
        left: auto;
        width: 80px;
        height: 1px;
      }
    }
  }
}
