* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  min-width: 320px;
  color: $color-black;
  line-height: 24px;
  font-family: 'Montserrat', sans-serif;
  overflow-x: hidden;

  .slick-arrow {
    position: absolute;
    top: 100px;
    padding: 0;
    width: 21px;
    height: 30px;
    font-size: 0;
    background-repeat: no-repeat;
    background-color: transparent;
    background-position: 50%;
    border: none;
    z-index: 9999;
    cursor: pointer;
    outline: none;
    opacity: 0.5;

    &:hover,
    &:focus {
      opacity: 1;
    }
  }

  .slick-prev {
    left: 20px;
    background-image: url("../img/left-arrow.svg");
  }

  .slick-next {
    right: 20px;
    background-image: url("../img/right-arrow.svg");

  }

  .slick-track {
    display: flex;
    align-items: center;
    min-height: 330px;
  }
}

ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active):not(:hover),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}

.title {
  margin-bottom: 15px;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;

  @media (min-width: $tablet-width) {
    margin-bottom: 40px;
    max-width: 423px;
    margin: 0 auto;
    font-size: 30px;
    line-height: 36px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 65px;
    font-size: 36px;
    line-height: 46px;
  }

  &::after {
    position: absolute;
    bottom: -12px;
    left: 20%;
    content: "";
    width: 60%;
    height: 2px;
    background-color: $color-brown;

    @media (min-width: $tablet-width) {
      left: 40px;
      width: 80%;
    }
  }
}

.subtitle {
  margin-top: 15px;
  font-size: 10px;
  text-align: center;
  letter-spacing: 1px;
  color: $color-brown;
  text-transform: uppercase;

  @media (min-width: $desktop-width) {
    font-size: 14px;

    margin-top: 0;
  }
}
