.goods {
  padding-bottom: 50px;

  &__list {
    margin-top: 30px;
    overflow: hidden;

    @media (min-width: $desktop-width) {
      margin-bottom: 60px;

      &:last-child {
        margin-bottom: 0;
      }
    }

  }

  &__item {
    position: relative;
    margin: 0 auto;
    margin-bottom: 20px;
    width: 300px !important;
    display: flex !important;
    flex-direction: column;
    align-items: center;
    outline: none;

    @media (min-width: $desktop-width) {
      width: 250px !important;
    }

    &--sale {

      &::before {
        padding: 9px;
        padding-top: 0;
        padding-bottom: 0;
        content: "SALE";
        position: absolute;
        top: 0;
        left: 0;
        font-size: 10px;
        letter-spacing: 1px;
        color: #fff;
        background-color: #c16774;
      }
    }
  }

  &__pic {
    width: 200px;
    height: 198px;
  }

  &__label {
    margin-bottom: 10px;
    font-size: 14px;
    text-align: center;

    @media (min-width: $tablet-width) {
      margin-top: 5px;
      font-size: 16px;
    }
  }

  &__info {
    font-size: 11px;
    line-height: 21px;

    @media (min-width: $tablet-width) {
      font-size: 13px;
    }

  }

  &__price {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 10px;
    width: 70%;

    @media (min-width: $desktop-width) {
      line-height: 24px;
    }
  }

  &__cost {
    font-size: 11px;
    font-weight: bold;

    @media (min-width: $tablet-width) {
      font-size: 13px;
    }

    &--old {
      color: $color-pink;
      text-decoration: line-through;
    }
  }
}
