.articles {
  padding-top: 30px;
  padding-bottom: 50px;
  padding-left: 10px;
  padding-right: 10px;

  .title {
    margin-bottom: 30px;

    @media (min-width: $desktop-width) {
      margin-bottom: 60px;
    }
  }

  &__item {
    padding-bottom: 15px;
    margin-bottom: 15px;
    min-height: 180px;
    border-bottom: 1px solid $color-grey;

    @media (min-width: $tablet-width) {
      margin-bottom: 30px;
    }

    &--pictured {
      background-repeat: no-repeat;
      background-position: 0 0;
      background-size: 160px 160px;
    }

    &--parfumers {
      background-image: url("../img/gentelmens.png");

      @media (min-width: $tablet-width) {
        background-size: 280px 280px;
      }

      .articles__title,
      .articles__date,
      .articles__text {
        padding-left: 100px;

        @media (min-width: $tablet-width) {
          padding-left: 220px;
        }
      }
    }

    &--korloff {
      background-image: url("../img/korloff.png");
      background-position: 150% top;
      background-size: 220px 200px;

      @media (min-width: $tablet-width) {
        background-position: 153% 0;
        background-size: 360px 340px;
      }

      .articles__title,
      .articles__date,
      .articles__text {
        padding-right: 120px;
      }
    }

    &--creed {
      background-image: url("../img/creed.png");
      background-position: -12% top;
      background-size: 160px 220px;

      @media (min-width: $tablet-width) {
        background-position: -3% 0;
        background-size: 200px 260px;
      }

      .articles__title,
      .articles__date,
      .articles__text {
        padding-left: 130px;
      }
    }
  }

  &__title {
    margin-bottom: 7px;
    font-size: 14px;
    line-height: 21px;

    @media (min-width: $tablet-width) {
      font-size: 16px;
    }

    @media (min-width: $desktop-width) {
      max-width: 550px;
    }
  }

  &__date {
    font-size: 11px;
    color: $color-brown;
    line-height: 21px;

    @media (min-width: $tablet-width) {
      font-size: 13px;
    }
  }

  &__text {
    margin-top: 7px;
    font-size: 10px;
    line-height: 21px;

    @media (min-width: $tablet-width) {
      font-size: 12px;
    }

    @media (min-width: $desktop-width) {
      max-width: 550px;
    }
  }

  &__inner {
    display: flex;
    justify-content: space-around;
  }

  &__column {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    @media (min-width: $desktop-width) {
      margin-right: 30px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__link {
    display: inline-block;
    width: 100%;
    color: $color-brown;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 10px;
    letter-spacing: 1px;
    text-align: center;
    opacity: 0.5;

    &:hover,
    &:focus {
      opacity: 1;
    }

    @media (min-width: $tablet-width) {
      font-size: 12px;
    }
  }
}
