.promo {

  @media (min-width: $desktop-width) {
    margin-right: 50px;
  }

  &__caption {
    align-self: flex-start;
    padding-top: 45px;
    margin-bottom: 10px;
    max-width: 240px;
    padding-left: 15px;
    font-size: 16px;
    letter-spacing: 1px;
    text-transform: uppercase;

    @media (min-width: $tablet-width) {
      padding-left: 45px;
      max-width: 440px;
      font-size: 30px;
      line-height: 36px;
    }

    @media (min-width: $desktop-width) {
      max-width: 762px;
      margin-top: 150px;
      font-size: 50px;
      line-height: 70px;
    }
  }

  &__wrapper {
    position: relative;
    width: 100%;
    min-height: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url("../img/model.png");
    background-size: cover;
    background-repeat: no-repeat;

    @media (min-width: $tablet-width) {
      min-height: 400px;
      background-position: 175px;
    }

    @media (min-width: $desktop-width) {
      margin-top: 62px;
      min-height: 549px;
      background-position: 300px;
    }
  }

  &__pic {
    width: 65px;
    height: 145px;

    @media (min-width: $tablet-width) {
      width: 100px;
      height: 230px;
    }

    @media (min-width: $desktop-width) {
      position: absolute;
      top: 30%;
      left: 65%;
      width: 110px;
      height: 280px;
    }

  }

  &__price {
    padding-top: 5px;
    font-size: 10px;
    font-weight: bold;
    letter-spacing: 0;

    @media (min-width: $tablet-width) {
      font-size: 12px;
    }

    @media (min-width: $desktop-width) {
      padding-left: 800px;
      padding-top: 50px;
      font-size: 13px;
    }
  }

  &__link {
    display: none;

    @media (min-width: $tablet-width) {
      display: block;
      position: absolute;
      bottom: 80px;
      left: 40px;
      font-size: 10px;
      letter-spacing: 1px;
      color: $color-brown;
      text-transform: uppercase;
      font-weight: bold;
      opacity: 0.5;

      &::before {
        content: "";
        position: absolute;
        bottom: -5px;
        width: 135px;
        height: 2px;
        background-color: $color-brown;

        @media (min-width: $desktop-width) {
          bottom: -10px;
          width: 246px;
        }
      }
    }

    @media (min-width: $desktop-width) {
      bottom: 120px;
      left: 55px;
      font-size: 14px;
    }

    &:hover,
    &:focus {
      opacity: 1;
    }
  }

  &__controls {
    align-self: flex-start;
    padding-left: 15px;

    @media (min-width: $tablet-width) {
      padding-left: 45px;
    }

    @media (min-width: $desktop-width) {
      padding-left: 315px;
      padding-top: 80px;
    }
  }

  #product-1:checked ~ label[for="product-1"],
  #product-2:checked ~ label[for="product-2"],
  #product-3:checked ~ label[for="product-3"] {
    background-color: $color-brown;
  }

  &__label {
    margin-right: 8px;
    display: inline-block;
    width: 10px;
    height: 10px;
    font-size: 0;
    vertical-align: top;
    background-color: transparent;
    border: 2px solid $color-brown;
    cursor: pointer;

    @media (min-width: $desktop-width) {
      margin-left: 30px;
    }
  }

  &__advice {
    display: none;

    @media (min-width: $desktop-width) {
      display: block;
      align-self: flex-end;
      position: absolute;
      right: -40px;
      top: -20px;
      padding: 27px;
      min-width: 332px;
      min-height: 285px;
      background-color: #f9f9f9;
      box-shadow: -32.453px 28.211px 54px rgba(36, 25, 5, 0.05);
    }
  }

  &__title {
    position: relative;
    font-size: 20px;
    letter-spacing: 1px;
    text-transform: uppercase;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -30px;
      width: 310px;
      height: 2px;
      background-color: $color-brown;
    }
  }

  &__list {
    margin-top: 70px;
  }

  &__item {

    a {
      font-size: 16px;
      line-height: 36px;
      color: $color-black;

      &:hover,
      &:focus {
        color: $color-brown;
      }
    }
  }
}
