.top-menu {

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: column;
  }

  &--closed {
    .top-menu__nav {
      display: none;

      @media (min-width: $desktop-width) {
        display: flex;
      }
    }

    .icon-menu--cross {
      display: none;
    }
  }

  &--opened {
    .top-menu__nav {
      display: block;
      padding-bottom: 10px;
    }

    .icon-menu {
      &--cross {
        display: block;
      }

      &--burger {
        display: none;
      }
    }
  }


  &__wrapper {
    position: relative;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (min-width: $desktop-width) {
      align-self: flex-end;
      width: 80%;
      order: 2;
    }
  }

  &__logo {
    opacity: 0.7;

    &:hover,
    &:focus {
      opacity: 1;
    }

    img {
      width: 150px;
      height: 90px;

      @media (min-width: $tablet-width) {
        width: 150px;
        height: 90px;
      }

      @media (min-width: $desktop-width) {
        width: auto;
        height: auto;
        position: absolute;
        top: 70px;
      }
    }
  }

  &__toggle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 54px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    outline: none;

    @media (min-width: $desktop-width) {
      display: none;
    }

    svg {
      fill: $color-brown;
    }
  }

  &__search {
    padding-bottom: 10px;
    width: 58%;
    border: none;
    border-bottom: 2px solid $color-grey;
    font-family: 'Montserrat', sans-serif;
    opacity: 0.7;
    outline: none;

    @media (min-width: $tablet-width) {
      width: 75%;
      font-size: 16px;
    }

    @media (min-width: $desktop-width) {
      margin-top: 10px;
      margin-bottom: 25px;
      width: 81%;
      font-size: 14px;
    }


    &::placeholder {
      font-size: 10px;
      letter-spacing: 1.5px;
      line-height: 24px;
      text-transform: uppercase;

      @media (min-width: $tablet-width) {
        font-size: 12px;
      }

      @media (min-width: $desktop-width) {
        font-size: 14px;
      }
    }

    &:hover,
    &:focus {
      opacity: 1;
    }
  }

  &__cart {
    position: relative;
    width: 60px;
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    cursor: pointer;
    outline: none;
    background-color: $color-brown;
    opacity: 0.7;

    @media (min-width: $tablet-width) {
      width: 80px;
      height: 74px;
    }

    @media (min-width: $desktop-width) {
      position: absolute;
      right: 48px;
      top: 20px;
      width: 90px;
      height: 84px;
    }

    &:hover,
    &:focus {
      opacity: 1;
    }

    &::after {
      position: absolute;
      content: "4";
      top: 5px;
      right: 5px;
      font-size: 10px;
    }

    svg {
      fill: $color-brown;
    }
  }

  &__nav {
    align-items: center;

    @media (min-width: $desktop-width) {
      display: flex;
      justify-content: space-between;
      order: 1;
    }
  }

  @media (min-width: $desktop-width) {

    &__site-items,
    &__user-items {
      flex-direction: row;

    }

    &__site-items {
      flex-grow: 1;
      padding-left: 30px;
      padding-right: 30px;
      border: 1px solid $color-grey;
      border-top: none;
      border-left: none;
    }

    &__user-items {
      padding-left: 30px;
      padding-right: 30px;
      border: 1px solid $color-grey;
      border-top: none;

      .top-menu__item,
      .top-menu__item:first-child {
        padding-right: 30px;
        border-right: 1px solid $color-grey;

        &:last-child {
          border-right: none;
          padding-right: 0;
        }
      }
    }

  }

  &__item {
    font-size: 18px;
    border-bottom: 1px solid $color-grey;
    border-top: 1px solid #efefef;
    cursor: pointer;

    @media (min-width: $desktop-width) {
      margin-right: 20px;
      border: none;
    }

    &:first-child {
      border-top: 1px solid $color-grey;

      @media (min-width: $desktop-width) {
        border: none;
      }
    }

    a {
      padding-top: 5px;
      padding-bottom: 5px;
      display: inline-block;
      width: 100%;
      color: $color-black;
      font-size: 14px;
      text-align: center;

      @media (min-width: $tablet-width) {
        font-size: 16px;
      }

      @media (min-width: $desktop-width) {
        padding-top: 18px;
        padding-bottom: 18px;
        font-size: 13px;
      }

      &:hover,
      &:focus {
        color: $color-brown;
      }
    }
  }

  &__contacts {
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (min-width: $desktop-width) {
      margin-bottom: 30px;
      order: 3;
    }
  }

  .icon-menu {
    opacity: 0.7;

    &:hover,
    &:focus {
      opacity: 1;
    }
  }

  &__list {

    @media (min-width: $tablet-width) {
      width: 50%;
      flex-direction: row;
    }

    @media (min-width: $desktop-width) {
      width: 80%;
    }

    li {
      display: flex;
      align-items: center;
      font-size: 10px;
      font-weight: bold;
      text-transform: uppercase;
      letter-spacing: 1px;

      @media (min-width: $tablet-width) {
        margin-right: auto;
        font-size: 12px;

        &:last-child {
          margin-right: 55px;
        }
      }

      @media (min-width: $desktop-width) {
        margin-right: 50px;
        font-size: 14px;

        &:last-child {
          margin-right: 0;
        }
      }

      svg {
        margin-right: 5px;
        width: 10px;
        height: 10px;
        fill: $color-brown;
      }
      
      a {
        color: $color-black;
        
        &:hover,
        &:focus {
          color: blue;
        }
      }
    }
  }
}
