.footer {
  padding-bottom: 20px;
  background-color: $color-black;

  &__menu {
    padding-top: 20px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 30px;
    color: #fff;
    border-bottom: 1px solid $color-gray;

    @media (min-width: $tablet-width) {
      display: flex;
      justify-content: space-between;
    }
  }

  &__title {
    margin-bottom: 20px;
    font-size: 12px;
    letter-spacing: 2px;
    text-transform: uppercase;
  }

  &__list {
    margin-bottom: 20px;
  }

  &__item {
    display: flex;
    align-items: center;
    font-size: 10px;
    line-height: 20px;

    &--bold {
      font-weight: bold;
    }

    a {
      color: #fff;
      opacity: 0.5;

      &:hover,
      &:focus {
        opacity: 1;
      }
    }

    svg {
      margin-right: 10px;
      fill: $color-brown;
    }
  }

  .icon-vb {
    margin-left: 10px;
    fill: #65bc54;
  }

  .icon-wp {
    fill: #8456a5;
  }

  &__subscribe {
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
  }

  &__input {
    padding-top: 5px;
    padding-bottom: 10px;
    margin-bottom: 10px;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    border: none;
    border-bottom: 2px solid $color-brown;
    background-color: transparent;
    outline: none;

    &::placeholder {
      letter-spacing: 1px;
      color: #fff;
      font-size: 10px;
      text-transform: uppercase;
    }
  }

  &__submit {
    margin-top: 10px;
    display: flex;
    justify-content: flex-end;
    text-transform: uppercase;
    color: $color-brown;
    font-size: 10px;
    letter-spacing: 1px;
    font-weight: bold;
    border: none;
    background-color: transparent;
    outline: none;
  }

  &__link {
    display: flex;
    align-items: center;
    font-size: 10px;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: bold;
    opacity: 0.5;

    &:hover,
    &:focus {
      opacity: 1;
    }

  }

  &__copywrite {
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    display: flex;
    flex-direction: column;
    color: #fff;
    font-size: 10px;
    text-align: center;

    @media (min-width: $tablet-width) {
      flex-direction: row;
      justify-content: space-between;
    }

  }

  &__developer {
    display: flex;
    align-items: center;
    justify-content: center;

    svg {
      margin-right: 5px;
      fill: $color-brown;
    }
  }

  .icon-inst {
    margin-right: 10px;
    width: 40px;
    height: 40px;
    border-radius: 24px;
    background-image: url("../img/icon-instagram.svg");
    background-size: 24px 24px;
    background-repeat: no-repeat;
    background-position: center;
    background-color: #fff;
    box-shadow: 0px 14px 46px rgba(51, 80, 64, 0.1);
  }
}
