.brands {

  .title {
    @media (min-width: $desktop-width) {
      margin-bottom: 30px;
    }
  }

  &__list {
    align-items: center;

    @media (min-width: $desktop-width) {
      margin-bottom: 20px;
      flex-direction: row;
      justify-content: space-around;
    }
  }

  &__item {
    display: flex;
    align-items: center;

    img {
      width: 170px;

      @media (min-width: $tablet-width) {
        width: 200px;
      }
    }

  }

  &__wrapper {
    padding-top: 10px;
    padding-left: 30px;
    padding-right: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;

    @media (min-width: $desktop-width) {
      padding-left: 100px;
    }
  }

  &__roster {
    margin-left: 23px;
    margin-right: 45px;
    width: 300px !important;
  }

  &__el {
    font-size: 14px;
    text-align: center;

    @media (min-width: $tablet-width) {
      font-size: 16px;
    }

    @media (min-width: $desktop-width) {
      line-height: 36px;
      text-align: left;
    }
  }

  .slick-track {
    min-height: 145px;
    justify-content: center;
  }

  .slick-arrow {
    top: 55px;
  }
}
